 chuyện về củ khoai tây"),_c('br'),_c('br'),_vm._v(" “Một ngày nọ, thầy giáo yêu cầu mỗi chúng tôi mang một túi nilông sạch và một bao tải khoai tây đến lớp. Sau đó, thầy bảo mỗi lần chúng tôi không tha thứ lỗi lầm cho người nào đó thì hãy chọn ra một củ khoai tây, viết tên người ấy và ngày tháng lên đó, rồi bỏ nó vào túi nilông. Sau vài ngày, túi của nhiều người trong lớp đã bắt đầu nặng dần."),_c('br'),_c('br'),_vm._v(" Sau đó, thầy lại yêu cầu chúng tôi phải luôn đeo cái túi đấy bên mình dù đi bất cứ đâu, ngủ hay làm việc. Sự phiền phức khi phải mang vác một cái túi chứa hàng chục củ khoai tây khiến chúng tôi càng cảm nhận rõ ràng gánh nặng tinh thần mà mình đang chịu đựng. Không những thế, chúng tôi còn phải luôn để tâm đến nó, nhớ đến nó và nhiều khi đặt nó ở những chỗ chẳng tế nhị chút nào."),_c('br'),_c('br')])]),_c('div',{staticClass:"page scolls",class:_vm.getCl(4),style:(_vm.getZindex(4)),on:{"click":function($event){return _vm.clicks(4)}}},[_c('div',{staticClass:"box-content"},[_vm._v("Qua thời gian, khoai tây bắt đầu phân huỷ thành một thứ chất lỏng nhầy nhụa và chúng tôi không muốn mang nó bên mình nữa…”"),_c('br'),_c('br'),_vm._v(" Tha thứ không đồng nghĩa với việc giữ những khúc mắc, đau khổ, và sự không hài lòng ở trong tâm. Làm như vậy, bạn chỉ đang kìm nén những nỗi đau lại. Bạn sẽ cứ nghĩ mãi về những chuyện không vui, những vết thương lòng mà không để ý tới viễn cảnh lạc quan hơn. Vì vậy, tha thứ trước tiên chính là buông bỏ những tiêu cực của bản thân mình. Bạn phải bao dung với con người mình thì mới biết cách bao dung với người khác."),_c('br'),_c('br')])]),_c('div',{staticClass:"page scolls",class:_vm.getCl(5),style:(_vm.getZindex(5)),on:{"click":function($event){return _vm.clicks(5)}}},[_c('div',{staticClass:"box-content"},[_vm._v(" Chúng ta thường cho rằng tha thứ là làm việc tốt với người được tha thứ, giống như ‘cho phép’ họ giũ bỏ những gánh nặng tinh thần. Thực chất, tha thứ là món quà cho chính bản thân bạn. Tha thứ là để quá khứ ở lại